<template>
  <div class="component">
    <video
      autoplay
      playsinline
      muted
      ref="videoElement"
      style="pointer-events: none"
      :class="{ show: videoShow }"
    ></video>
  </div>
</template>

<script>
import Hls from "hls.js";
// import videojs from 'video.js';

export default {
  props: {
    videoSrc: String,
    playsinline: {
      type: Boolean,
      default: false
    },
    controls: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    videoSrc(val) {
      this.videoShow = false
      this.startStream();
    }
  },
  methods: {
    startStream() {


      if (this.hls) this.hls.destroy();
      // console.log(MediaSource.addSourceBuffer)
      var videoElement = this.$refs.videoElement;


      if (!this.init) {
        videoElement.addEventListener("webkitendfullscreen", (e) => {
          this.$emit("ios-exit-fullscreen");
          // console.log("emit exit event");
        });
      }
      // console.log(iOSSafari||safari)
      // console.log(ua)
      if (this.isIosSafari || this.isSafari) {
        if (!this.init) {

          videoElement.addEventListener('playing', () => {
            videoElement.currentTime = videoElement.buffered.end(0);
            console.log('playing')

          })
          videoElement.addEventListener('waiting', () => {
            console.log('waiting')
          })
          videoElement.addEventListener('stalled', () => {
            console.log('stalled')
            this.startStream();
          })
          videoElement.addEventListener("loadeddata", (e) => {
            this.videoShow = true;
          })
        }
        videoElement.src = this.videoSrc;
        // videoElement.play();


      } else if (Hls.isSupported()) {
        var config = {
          enableWorker: true,
          // maxBufferLength: 1,
          // liveBackBufferLength: 0,
          liveSyncDuration: 0,
          liveMaxLatencyDuration: 1,
          liveDurationInfinity: true,
          highBufferWatchdogPeriod: 1,
        };
        this.hls = new Hls(config);
        this.hls.attachMedia(videoElement);
        this.hls.on(Hls.Events.MEDIA_ATTACHED, () => {
          this.hls.loadSource(this.videoSrc);
          this.hls.on(Hls.Events.MANIFEST_PARSED, () => {
            // console.log("parsed");
            // console.log(this.hls);
            videoElement.play();
            setTimeout(() => {
              this.videoSize.w = videoElement.getBoundingClientRect().width;
              this.videoSize.h = videoElement.getBoundingClientRect().height;
            }, 500);
          });
        });
        this.hls.on(Hls.Events.ERROR, (event, data) => {
          // console.log("HLS Error: ", data);
          if (data.fatal) {
            switch (data.type) {
              case Hls.ErrorTypes.NETWORK_ERROR:
                // try to recover network error
                console.log("fatal network error encountered, try to recover");
                this.restartStream();
                break;
              case Hls.ErrorTypes.MEDIA_ERROR:
                console.log("fatal media error encountered, try to recover");
                this.restartStream();
                break;
              default:
                // cannot recover
                console.log("HLS cannot recover, destroying");
                this.hls.destroy();
                break;
            }
          }
        });
        if (!this.init) {

          videoElement.addEventListener("ended", (e) => {
            // console.log("ended");
          });
          videoElement.addEventListener("abort", (e) => {
            // console.log("abort");
          });
          videoElement.addEventListener("emptied", (e) => {
            // console.log("emptied");
          });
          videoElement.addEventListener("waiting", (e) => {
            // console.log("waiting");
          });
          videoElement.addEventListener("playing", (e) => {
            // console.log("playing");
          });
          videoElement.addEventListener("play", (e) => {
            // this.videoShow = true;
          });
          videoElement.addEventListener("loadeddata", (e) => {
            this.videoShow = true;
          })
        }
      }
      this.init = true;
    },

    restartStream() {
      // this.hls.recoverMediaError();
      this.startStream();
    },
  },
  computed: {

  },
  data() {
    return {
      hls: null,
      videoSize: {
        w: 0,
        h: 0,
      },
      refreshTimeoutMs: 5000,
      init: false,
      videoShow: true,
      isIosSafari: false,
      isSafari: false,
      options: {
        type: Object,
        default() {
          return {};
        }
      }
    };
  },
  unmounted() {
    if (this.hls) {
      this.hls.destroy();
    }
  },

  mounted() {
    var ua = window.navigator.userAgent;
    var iOS = !!ua.match(/iPad/i) || !!ua.match(/iPhone/i);
    var webkit = !!ua.match(/WebKit/i);
    var iOSSafari = iOS && webkit && !ua.match(/CriOS/i);
    var safari = !!ua.match(/Safari/i) && !ua.match(/Chrome/i);
    this.isIosSafari = iOSSafari;
    this.isSafari = safari;
    this.startStream();
  },
};
</script>

<style lang="less" scoped>
.component {
  width: 100%;
  height: 100%;
}
video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
}
.show {
  animation-duration: 0.4s;
  animation-timing-function: ease-out;
  animation-name: fadeIn;
  opacity: 1;
}
@keyframes fadeIn {
  from {
    transform: scale(0.9);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}
//
</style>