<template>
  <div>
    <h1>Camera Stream Configurations</h1>
    <div class="table-header">
      <div class="stream-count">
        Total streams: <Badge :value="streams.length"></Badge>
      </div>
      <Button icon="pi pi-plus" @click="addNewStream()" label="Add new" />
    </div>
    <DataTable :value="streams">
      <Column style="width: 40px" field="alias" header="">
        <template class="foo" #body="slotProps">
          {{ slotProps.index + 1 }}
        </template>
      </Column>
      <Column field="alias" header="Camera Alias">
        <template #body="slotProps">
          <div class="alias" @click="viewStream(slotProps.data)">
            {{
              slotProps.data.alias.charAt(0).toUpperCase() +
              slotProps.data.alias.slice(1)
            }}
          </div>
        </template>
      </Column>

      <Column field="primary" header="Primary">
        <template #body="slotProps">
          <div class="primary">
            <RadioButton
              name="city"
              :value="slotProps.data.alias"
              v-model="primaryStream"
            />
          </div>
        </template>
      </Column>

      <Column field="streamUri" header="Stream URI"></Column>
      <Column field="running" header="Running Status">
        <template #body="slotProps">
          <badge
            :value="getBadgeValueAndSeverity(slotProps.data).value"
            :severity="getBadgeValueAndSeverity(slotProps.data).severity"
          ></badge>
        </template>
      </Column>
      <Column field="action" header="Actions">
        <template #body="slotProps">
          <div class="row-buttons">
            <Button
              icon="pi"
              class="p-button-secondary p-button-rounded p-button-outlined"
              :disabled="slotProps.data.running === null"
              @click="removeStream(slotProps.data, $event)"
            >
              <font-awesome-icon icon="trash" />
            </Button>
            <Button
              icon="pi"
              class="p-button-rounded p-button-secondary p-button-outlined"
              @click="viewStream(slotProps.data)"
              :disabled="slotProps.data.running === null"
            >
              <ProgressSpinner v-if="slotProps.data.running === null" />
              <font-awesome-icon v-else icon="eye" />
            </Button>

            <Button
              v-if="slotProps.data.running"
              icon="pi"
              class="p-button-rounded p-button-secondary p-button-outlined"
              :disabled="slotProps.data.running === null"
              @click="stopStream(slotProps.data)"
            >
              <font-awesome-icon icon="stop" />
            </Button>
          </div>
        </template>
      </Column>
    </DataTable>
    <Toast position="top-center" />
    <Dialog
      class="dialog new-stream"
      header="Add New Stream"
      v-model:visible="newStreamDisplayModal"
      :modal="true"
      :closeOnEscape="true"
    >
      <div class="content">
        <div class="p-field">
          <label for="alias">Stream Alias</label>
          <InputText
            id="alias"
            v-model="newStreamData.alias"
            placeholder="Garage, Room, etc."
          />
        </div>

        <div class="p-field">
          <label for="feed">Rtsp Feed</label>
          <InputText
            id="feed"
            v-model="newStreamData.rtspUri"
            placeholder="rtsp://user:pass@192.168.0.150"
          />
        </div>
      </div>
      <template #footer>
        <div class="processingStatus" v-show="newStreamProcessing">
          <ProgressSpinner />
          Processing...
        </div>
        <div class="buttons">
          <Button
            :disabled="
              newStreamProcessing ||
              newStreamData.alias == '' ||
              newStreamData.rtspUri == ''
            "
            label="Submit"
            autofocus
            @click="createStream()"
          />
        </div>
      </template>
    </Dialog>

    <Dialog
      class="dialog view-stream"
      v-model:visible="streamViewerDisplayModal"
      :modal="true"
      :closeOnEscape="true"
      @hide="refreshStreamList()"
    >
      <template #header>
        <h3>{{ activeVideoAlias }}</h3>
        <Button
          icon="pi"
          class="p-button-secondary p-button-rounded p-button-outlined"
          @click="restartStream()"
        >
          <font-awesome-icon icon="sync" />
        </Button>
      </template>
      <hls-video
        v-on:ios-exit-fullscreen="streamViewerDisplayModal = false"
        ref="videoStream"
        :video-src="activeVideoSrc"
        @click="playFullscreen"
      ></hls-video>
    </Dialog>

    <ConfirmPopup></ConfirmPopup>
  </div>
</template>

<script>

import ls from "localforage";
// var domain = "localhost";
// var domain = window.location.host.split(':')[0];

// var domain = "localhost:3000";

export default {
  name: "Settings",
  data() {
    return {
      getListURI: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/list",
      stopURI: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/stop",
      startURI: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/start",
      streamUri: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort,
      websocketUri: "ws://" + window.destinations.activeDomain + ":" + window.destinations.activeWsPort,

      newStreamDisplayModal: false,
      streamViewerDisplayModal: false,
      streams: [],
      newStreamData: {
        alias: "foo",
        rtspUri: "bar",
      },
      newStreamProcessing: false,
      activeVideoSrc: null,
      activeVideoAlias: null,
      listRefreshInterval: -1,
      primaryStream: null
    };
  },
  methods: {
    test(v) {
      console.log(v);
    },
    restartStream() {
      this.$refs.videoStream.restartStream();
    },
    getBadgeValueAndSeverity(data) {
      if (data.running === true)
        return { value: "Running", severity: "success" };
      if (data.running === false)
        return { value: "Stopped", severity: "danger" };
      if (data.running === null)
        return { value: "Starting", severity: "warning" };
    },
    async refreshStreamList(payload) {

      var primaryStreamConfigUri = "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/getPrimaryStreamAlias";
      const response = await fetch(primaryStreamConfigUri)

      var responseJson = await response.json();
      this.primaryStream = responseJson.primaryStreamAlias;
      if (payload) {
        this.streams = payload;
      } else {
        this.streams = await this.getStreams();
      }
      //sort
      if (this.streams.sort) this.streams.sort((a, b) => (a.created > b.created ? 1 : -1));
    },
    async stopStream(data) {
      var token = await ls.getItem('token');
      const response = await fetch(this.stopURI, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          alias: data.alias,
          remove: false,
          token: token
        }),
      });
      if (response.ok) {
        this.refreshStreamList();
      } else {
        this.$toast.add({
          severity: "error",
          summary: "Stream stop failed",
          detail:
            "Stream for '" +
            data.alias +
            "' can't be stopped. Reason: " +
            response.statusText,
          life: 3000,
        });
      }
    },
    async createStream() {
      var token = await ls.getItem('token');
      this.newStreamProcessing = true;
      const response = await fetch(this.startURI, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          rtspUri: this.newStreamData.rtspUri,
          alias: this.newStreamData.alias,
          token: token
        }),
        credentials: 'include'
      });
      if (response.ok) {
        this.refreshStreamList();
      } else {
        this.$toast.add({
          severity: "error",
          summary: "Stream creation failed",
          detail:
            "Stream for URI of '" +
            this.newStreamData.rtspUri +
            "' can't be created. Reason: " +
            response.statusText,
          life: 3000,
        });
      }
      this.newStreamDisplayModal = false;
      this.newStreamProcessing = false;
    },
    async addNewStream() {
      this.newStreamData.alias = "";
      this.newStreamData.rtspUri = "";
      this.newStreamDisplayModal = true;
    },
    async getStreams() {
      const responseJson = await window.HC_getList();
      return responseJson;
    },
    async removeStream(data, ev) {
      this.$confirm.require({
        target: ev.currentTarget,
        message: "Stop and remove stream?",
        accept: () => {
          remove();
        },
        reject: () => { },
      });

      var remove = async () => {
        var token = await ls.getItem('token');

        const alias = data.alias;
        const response = await fetch(this.stopURI, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            alias: alias,
            remove: true,
            token: token
          }),
        });
        if (response.ok) {
          this.refreshStreamList();
        } else {
          this.$toast.add({
            severity: "error",
            summary: "Stream removal failed",
            detail:
              "Stream with alias of '" +
              alias +
              "' can't be removed. Reason: " +
              response,
            life: 3000,
          });
        }
      };
    },
    async viewStream(data) {


      if (data.running) {
        this.streamViewerDisplayModal = true;
        this.activeVideoAlias = data.alias;
        var streamSrc = this.streamUri + "/" + data.streamUri;
        this.activeVideoSrc = streamSrc;
        setTimeout(() => {
          this.playFullscreen(null, document.querySelector('video'))
        }, 100);
        // alert(document.querySelector('video'))

      } else {
        // data.running = null;
        const response = await fetch(this.startURI, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          credentials: 'include',
          body: JSON.stringify({
            alias: data.alias,
          }),
        });
        if (response.ok) {
          data.running = true;
          this.viewStream(data);
        }
        /**
         * const response = await fetch(this.startURI, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uri: this.newStreamData.rtspUri,
          alias: this.newStreamData.alias,
        }),
      });
      if (response.ok) {
        this.refreshStreamList();
         */
      }
    },
    connectToWebSocket() {
      var reconnectInterval = -1;
      this.connection = new WebSocket(this.websocketUri);
      this.connection.onmessage = (event) => {
        var payload = JSON.parse(event.data);
        if (payload.type == "stream-update") {
          this.refreshStreamList(payload.data);
        }
      };

      this.connection.onopen = (event) => {
        console.log("Successfully connected to the webcam websocket server...");
      };

      //reconnect
      this.connection.onclose = (event) => {
        reconnectInterval = setInterval(() => {
          clearInterval(reconnectInterval);
          this.connectToWebSocket();
        }, 300);
      };
    },
    async updatePrimaryStream(alias) {
      var updateUri = "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/setPrimaryStreamAlias"
      const response = await fetch(updateUri, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          alias: alias,
        }),
      });
    },
    playFullscreen(e, target) {

      var vidEl;
      if (e == null) {
        vidEl = target;
      } else {
        vidEl = e.currentTarget.querySelector('video');
      }
      vidEl.webkitSetPresentationMode('fullscreen');
      vidEl.addEventListener('webkitendfullscreen', () => {
        setTimeout(() => {
          vidEl.play();
        }, 1000);
      })
    }
  },
  watch: {
    primaryStream(val) {
      //update server
      this.updatePrimaryStream(val);
    }
  },
  async mounted() {
    this.refreshStreamList();
    this.connectToWebSocket();
  }
};
</script>

