import { createRouter, createWebHistory } from 'vue-router'
import Settings from '../views/Settings.vue'
import ls from "localforage";
import Cookies from 'js-cookie';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/dash-sm.vue')
  },
  {
    path: '/settings',
    name: 'Settings',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/Settings.vue')
  },
  {
    path: '/all',
    name: 'All Streams',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/All.vue')
  },
  {
    path: '/dash',
    name: 'dash',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/dash-sm.vue')
  },
  {
    path: '/scratch',
    name: 'scratch',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/Scratch.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/login.vue')
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

//validate token


router.beforeEach(async (to, from, next) => {
  var query = to.query || from.query;
  var validToken = await window.HC_validateToken();
  if (to.name !== 'Login' && !validToken) {
    console.log('redirecting to login...')
    next({ name: 'Login', query: query })
  }
  else next()
})


export default router
